import { createSlice } from "@reduxjs/toolkit"; // import createSlice t reduxToolkit  to mi 1 reducer(slice)

export const FormReducer = createSlice({
  name: "feedback",
  initialState: {
    firtName: "",
    lastName: "",
    telNum: "",
    email: "",
    agree:"false",
    contactType: "Tel.",
    message: "",
  },
  reducers: {
    // reducer  y chnh l cng nhn thc thi cc action
    setStatefeedback: (state, action) => {
      // y l action
      const newstate = { ...state };
      // state  y chnh l d liu trong initialState
      return { ...newstate, [action.payload.nameInput]: action.payload.value };
      // action l trng thi dispatch truyn vo t s kin bn ngoi trn UI.

    },
  },
});

// export theo kiu bc buc.
export const { setStatefeedback } = FormReducer.actions; // export action t reducer ra  s dng.

export default FormReducer.reducer; // export contactReducer trc tip di chnh n.
