import { configureStore } from "@reduxjs/toolkit"; // dng function configureStore api t toolkit  to mi Store
import DataContactReducer from "./reducer"; // export contactReducer trc tip
import userReducer from "./reducerThunk";
import FormReducer from "./reducerForms";

export const store = configureStore({
  reducer: {
    dataContact: DataContactReducer,
    user: userReducer,
    form: FormReducer,
  },
});
